:root {
  --my-account-tab-list-item-color: #404040; }

.MyAccountTabListItem {
  --border-width: 1px;
  --text-shadow: none;
  padding-left: 0;
  margin-bottom: 0; }
  .MyAccountTabListItem::before {
    content: none; }
  .MyAccountTabListItem:last-child {
    margin-bottom: 0; }
  @media (min-width: 811px) {
    .MyAccountTabListItem_isActive, .MyAccountTabListItem:hover {
      --border-width: 2px;
      --text-shadow:
                -.02ex 0 var(--my-account-tab-list-item-color),
                .02ex 0 var(--my-account-tab-list-item-color); } }
  @media (max-width: 810px) {
    .MyAccountTabListItem_isActive {
      display: none; } }
  .MyAccountTabListItem-Button {
    padding: 10px 0;
    min-width: 100%;
    min-height: 38px;
    text-align: left;
    text-shadow: var(--text-shadow);
    letter-spacing: .03ex;
    font-size: 1.4rem; }
    @media (min-width: 811px) {
      .MyAccountTabListItem-Button {
        padding: 10px 2.4rem;
        min-width: 250px; } }
    .MyAccountTabListItem-Button::after {
      content: '';
      position: absolute;
      top: 0;
      right: 1.2rem;
      bottom: 0;
      width: 6px;
      height: 6px;
      margin: auto;
      transform: rotate(45deg);
      border-right: var(--border-width) solid var(--my-account-tab-list-item-color);
      border-top: var(--border-width) solid var(--my-account-tab-list-item-color); }
      @media (max-width: 810px) {
        .MyAccountTabListItem-Button::after {
          right: 1.4rem; } }
      @media (min-width: 811px) {
        .MyAccountTabListItem-Button::after {
          right: 2.4rem; } }
