.MyAccountOrderPopup-Address, .MyAccountOrderPopup-Products {
  margin-bottom: 1.2rem; }
  @media (min-width: 811px) {
    .MyAccountOrderPopup-Address, .MyAccountOrderPopup-Products {
      margin-top: 2.4rem; } }
  @media (max-width: 810px) {
    .MyAccountOrderPopup-Address, .MyAccountOrderPopup-Products {
      margin-bottom: 2.8rem; } }

.MyAccountOrderPopup-Products {
  width: 100%;
  margin-top: 1.2rem;
  table-layout: initial; }
  @media (max-width: 810px) {
    .MyAccountOrderPopup-Products {
      margin-top: 1.4rem; } }
  .MyAccountOrderPopup-Products td,
  .MyAccountOrderPopup-Products th {
    vertical-align: middle; }

.MyAccountOrderPopup-Thumbnail {
  width: 60px;
  height: 60px;
  padding: 0; }

.MyAccountOrderPopup-Row {
  height: 80px;
  padding: 10px 0; }
  .MyAccountOrderPopup-Row:nth-child(even) img {
    mix-blend-mode: multiply; }

.MyAccountOrderPopup-Content {
  min-height: 200px; }
  @media (min-width: 811px) {
    .MyAccountOrderPopup-Content {
      display: grid;
      grid-gap: 2.4rem; } }

.MyAccountOrderPopup h4 {
  font-size: 1.44rem; }
  @media (max-width: 810px) {
    .MyAccountOrderPopup h4 {
      font-size: 1.68rem; } }

.MyAccountOrderPopup dt {
  font-weight: bold; }

.MyAccountOrderPopup dl {
  display: grid;
  margin-top: 1.2rem;
  grid-template-columns: 1fr 1fr;
  grid-gap: .6rem; }
  @media (max-width: 810px) {
    .MyAccountOrderPopup dl {
      margin-top: 1.4rem;
      grid-gap: .7rem;
      margin-bottom: 2.8rem; } }

.MyAccountOrderPopup td,
.MyAccountOrderPopup th {
  min-width: 0; }
