.MyAccountMyOrders {
  display: grid;
  grid-gap: 2.4rem;
  grid-template-columns: 1fr; }
  @media (max-width: 810px) {
    .MyAccountMyOrders {
      grid-gap: 2.8rem; } }
  @media (min-width: 811px) {
    .MyAccountMyOrders-Button {
      position: absolute;
      top: -55px;
      right: 0; } }
  .MyAccountMyOrders-Table {
    width: 100%;
    min-width: 100%;
    max-width: 100%; }
  @media (max-width: 810px) {
    .MyAccountMyOrders td,
    .MyAccountMyOrders th {
      min-width: 0; } }
