:root {
  --my-account-content-background: #fff;
  --my-account-wrapper-padding-mobile: 1.4rem; }
  @media (min-width: 811px) {
    :root {
      --my-account-background: var(--secondary-base-color); } }

.MyAccount {
  background: var(--my-account-background); }
  .MyAccount-Wrapper {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto 1fr;
    grid-column-gap: 2.4rem;
    grid-row-gap: 0;
    padding-top: 1.2rem;
    padding-bottom: 1.2rem; }
    @media (max-width: 810px) {
      .MyAccount-Wrapper {
        grid-column-gap: 2.8rem;
        padding: 0;
        grid-template-columns: 1fr; } }
  .MyAccount-TabContent {
    background: var(--my-account-content-background);
    padding: 1.2rem; }
    @media (max-width: 810px) {
      .MyAccount-TabContent {
        padding: var(--my-account-wrapper-padding-mobile); } }
    @media (min-width: 811px) {
      .MyAccount-TabContent {
        grid-row: span 2;
        padding: 2.4rem; } }
  .MyAccount-Heading {
    font-size: 2rem;
    font-weight: 400;
    text-transform: none;
    margin: 0 0 2.4rem; }
    @media (max-width: 810px) {
      .MyAccount-Heading {
        margin: 0 0 2.8rem;
        display: none; } }
  @media (max-width: 810px) {
    .MyAccount-Button,
    .MyAccount button {
      width: 100%; } }
