.MyAccountDashboard {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 2.4rem; }
  @media (max-width: 810px) {
    .MyAccountDashboard {
      grid-gap: 2.8rem; } }
  @media (min-width: 811px) {
    .MyAccountDashboard {
      grid-template-columns: 1fr 1fr;
      grid-gap: 4.8rem; } }
  @media (min-width: 811px) {
    .MyAccountDashboard-DefaultAddress {
      grid-row: 2; } }
  @media (min-width: 811px) {
    .MyAccountDashboard-CustomerData {
      grid-column: span 2; } }
  .MyAccountDashboard-Info {
    margin-bottom: 5px; }
  .MyAccountDashboard button + button {
    margin-top: 1.2rem; }
    @media (max-width: 810px) {
      .MyAccountDashboard button + button {
        margin-top: 1.4rem; } }
    @media (min-width: 811px) {
      .MyAccountDashboard button + button {
        margin-left: 1.2rem; } }
