@media (min-width: 811px) {
  :root {
    --my-account-tab-list-background: #fff; } }

@media (min-width: 811px) {
  .MyAccountTabList {
    padding: 1.2rem 0;
    background-color: var(--my-account-tab-list-background); } }

@media (min-width: 811px) {
  .MyAccountTabList-ExpandableContentContent {
    margin-top: 0; } }

@media (max-width: 810px) {
  .MyAccountTabList-ExpandableContentContent_isContentExpanded {
    padding: 0 1.4rem 1.4rem; } }

@media (min-width: 811px) {
  .MyAccountTabList-ExpandableContentButton {
    display: none; } }

.MyAccountTabList-ExpandableContentButton::before, .MyAccountTabList-ExpandableContentButton::after {
  right: 2.4rem; }
  @media (max-width: 810px) {
    .MyAccountTabList-ExpandableContentButton::before, .MyAccountTabList-ExpandableContentButton::after {
      right: 2.8rem; } }
