.MyAccountAddressBook {
  display: grid;
  grid-gap: 2.4rem;
  grid-template-columns: 1fr; }
  @media (max-width: 810px) {
    .MyAccountAddressBook {
      grid-gap: 2.8rem; } }
  @media (min-width: 811px) {
    .MyAccountAddressBook-Button {
      position: absolute;
      top: -55px;
      right: 0; } }
  .MyAccountAddressBook button + button {
    margin-left: 1.2rem; }
    @media (max-width: 810px) {
      .MyAccountAddressBook button + button {
        margin-top: 1.4rem;
        margin-left: 0; } }
